      fullStyle: ['/*\n* Dear LJ\n* \u90FD\u8BF4\u7406\u79D1\u751F\u7684\u6D6A\u6F2B\u662F\u6CE5\u77F3\u6D41\n* \u90A3\u4E48\u4F5C\u4E3A\u4E00\u4E2A\u4E0D\u6298\u4E0D\u6263\u7684\u7406\u79D1\u751F\n* \u8FD9\u6B21\u6211\u8981\u7528\u6211\u6240\u5B66\u7684\u77E5\u8BC6\u4E66\u5199\u7406\u79D1\u751F\u7684\u6D6A\u6F2B\n* \u73B0\u5728\u5C31\u8BA9\u6211\u7528\u4EE3\u7801\u6765\u4E3A\u4F60\u5448\u73B0\n* Show time, just enjoy\u2764\uFE0F\n*/\n\n/* begin coding */\n* {\n  transition: all .3s;\n}\n/* \u65E2\u7136\u8981\u6D6A\u6F2B, \u767D\u8272\u80CC\u666F\u592A\u5355\u8C03\u4E86, \u90A3\u4E48\u4EC0\u4E48\u989C\u8272\u6D6A\u6F2B\u5462 */\nhtml {\n  color: rgb(255,69,0);\n  background: rgb(255,182,193);\n}\n/* \u4FEE\u6539\u4E0B\u5E03\u5C40 */\n.styleEditor {\n  padding: .5em;\n  border: 1px solid;\n  overflow: auto;\n  width: 90vw;\n  margin: 2.5vh 5vw;\n  height: 90%;\n}\n\n.styleEditor {\n  height: 50%;\n  position: fixed; left: 0; top: 0;\n}\n/* \u8BA9\u4EE3\u7801\u597D\u770B\u4E9B */\n.token.selector{\n  color: rgb(106,90,205);\n}\n.token.property{\n  color: rgb(255,0,255);\n}\n.token.punctuation{\n  color: white;\n}\n.token.function{\n  color: rgb(219,112,147);\n}\n\n/* \u6765\u70B93D\u6548\u679C */\nhtml{\n  perspective: 1000px;\n}\n.styleEditor {\n  transform: rotateX(-10deg) translateZ(-50px) ;\n}\n\n/* \u63A5\u4E0B\u6765\u51C6\u5907\u4E00\u5F20"\u7EB8"\uFF0C\u8BE5\u5F00\u59CB\u771F\u6B63\u7684\u8868\u6F14\u4E86 */\n.editor{\n  position: fixed;\n  top:50%; left: 0;\n  padding: .5em;  margin: 2.5vh;\n  width: 90vw; height: 45%;\n  border: 1px solid; border-color: rgba(255,52,179,0.5);\n  background: rgba(255,165,0,0.3); color: rgb(148,0,211);\n  overflow: auto;\n}\n\n/* \u597D\u4E86\uFF0C\u4E0B\u9762\u8BF7\u542C\u6211\u8BF4 */\n\n\n', '\n/* ps: \u8FD9\u5F20"\u7EB8"\u662F\u53EF\u4EE5\u4E0A\u4E0B\u6ED1\u52A8\u7684\u54E6\n * \u73B0\u5728\u8FD9\u5F20\u7EB8\u6709\u70B9\u8FC7\u4E8E\u5355\u8C03, \u6211\u4EEC\u6765\u628A\u5B83\u8F6C\u6210 HTML\n */\n', '\n/* \u5148\u6765\u70B9\u6837\u5F0F */\n.editor {\n  background: rgba(255,165,0,0.3); color: rgb(148,0,211);\n  padding: .5em;\n  margin: 2.5vh 5vw;\n  top:5%; left: 0;\n  width: 90vw; height: 90%;\n  position: fixed;\n  border: 1px solid; border-color: rgba(255,52,179,0.5);\n  overflow: auto;\n}\n.styleEditor {\n  opacity: 0;\n}\n.editor h2{\n  display: inline-block;\n  border-bottom: 1px solid;\n  margin: 1em 0 .5em;\n}\n'],
